@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.category-box{
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: .625rem;
    gap: .625rem;
}

.input{
    display: inline-block; 
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    padding-left: .625rem;
    font-size: 1.2rem;
    font-family: 'Roboto', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 400;
    color: #888;
    border: 1px solid #CCCCCC; 
}

.input:focus{
    outline-color: rgb(97, 230, 201);
    outline-width: .03125rem;
}
.grid-item{
    width: 90%;
    min-width: 28%;
    background-color: #22232ede; 
    height: -webkit-min-content; 
    height: -moz-min-content; 
    height: min-content;
    border-radius: 5px;
}

.grid-item__image{
    max-width: 100%;
}

.grid-item__title{
    color: #FFFFFF;
    font-family: Consolas, 'Roboto', 'Segoe UI', sans-serif;
    font-weight: 800;
    font-size: 1.1rem;
    margin: 0;
    padding: 1.5rem 0.1rem 1.5rem 0.1rem;
    text-align: center;
}

@media all and (max-width: 588px)
{
    .grid-item__image{
        width: 100%;
    }

    .grid-item__title{
        color: #FFFFFF;
        font-family: Consolas, 'Roboto', 'Segoe UI', sans-serif;
        font-weight: 400;
        font-size: 0.9rem;
        margin: 0;
        padding: 1.5rem 0.1rem 1.5rem 0.1rem;
    }
}

@media all and (max-width: 996px){
    .grid-item{
        width: 100%;
        background-color: #22232ede; 
        height: -webkit-min-content; 
        height: -moz-min-content; 
        height: min-content;
        border-radius: 5px;
    }
}
.categories-grid{
    display: grid;
    grid-template-columns: repeat(auto-fit, 400px);
    grid-auto-rows: minmax(auto-fit, 400px);
    justify-items: center; 
    grid-gap: 15px; 
    gap: 15px;
    overflow: hidden;
    grid-auto-flow: dense;
}

@media all and (max-width: 996px)
{
    .categories-grid{
        align-items: center;
        justify-content: center;
        padding: 0;
    }
}
@media all and (max-width: 588px)
{
    .categories-grid{
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 0;
        align-items: center;
    }
    
}
*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --tw-text-opacity: 1;
    --black-text-color: rgba(107, 114, 128, var(--tw-text-opacity));
}

html {
    padding: 4rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #444444;
    background: #FAFAF9;
}

.main-header {
    font-size: calc(1.875rem + 2vh);
    display: block;
    padding-bottom: .9375rem;
    border-bottom: .125rem solid #AAAAAA;
}

.button {
    display: inline-block;
    padding: 1rem 2rem;
    background-color: #DDDDDD;
    border: 0;
    border-radius: 0.625rem;
    cursor: pointer;
    --tw-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
    box-shadow: 0 0 #0000, 0 0 #0000, var(--tw-shadow);
    box-shadow: var(--tw-ring-offset-shadow, 0 0 #0000), var(--tw-ring-shadow, 0 0 #0000), var(--tw-shadow);
    margin-top: 0;
    transition-property: transform;
    transition-duration: 0.5ms;
    color: #FAFAF9;
    margin-right: 1rem;
    margin-top: 0.3125rem;
    margin-bottom: 0.3125rem;
}

.button:focus {
    outline: 1px dotted;
    outline: 5px auto -webkit-focus-ring-color;
    outline: none;
}

.button:active {
    transform: translateY(4px);
}

.button__description {
    font-weight: bold;
    letter-spacing: 2px;
    font-size: 1rem;
    font-family: 'Roboto'
}

.button--success {
    background-color: rgba(99, 102, 241, 1);
}

.button--danger {
    background-color: #dd3544;
}

.button--primary {
    background-color: rgb(10, 122, 255);
}

.ordered-list {
    background-color: #555555;
    display: block;
    padding: 1.5rem;
    border-radius: 14px;
}

.ordered-list__item {
    display: block;
    padding: 1rem 2rem;
    background-color: #EEE;
    margin: 1rem;
    border-radius: 5px;
    font-weight: 800;
    text-transform: uppercase;
    cursor: pointer;
    transition-property: transform;
    transition-duration: 200ms;
}

.ordered-list__item--dark {
    background-color: #444444;
    color: #FAFAFA;
}

.ordered-list__item:hover {
    transform: scale(1.01);
}

@media all and (max-width: 910px) {

    html {
        padding: 0rem !important;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        color: #444444;
        background: #FAFAF9;
        font-size: 14px;
    }
}


